.stars {
  display: flex;

  &__star {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    height: 16px;
    width: 16px;
    margin-right: 4px;
  }

  &__star:last-child {
    margin-right: 0;
  }

  &--4 {
    .stars__star:nth-child(-n+4) {
      background-image: url(../images/star-active.svg);
    }
  }
}
